cols=\"12\" sm=\"6\" md=\"2\" class=\"pb-0 \">\r\n                                        <h4 class=\"pb-0 grey--text text--darken-2\">\r\n                                            Cod U. Operativa\r\n                                        </h4>\r\n                                        <v-text-field v-model=\"form.iduoper\" required dense outlined\r\n                                            :rules=\"obligatorioRules\"></v-text-field>\r\n                                    </v-col>\r\n                                    <v-col cols=\"12\" sm=\"6\" md=\"5\" class=\"pb-0\">\r\n                                        <h4 class=\"pb-0 grey--text text--darken-2\">\r\n                                            Nombre\r\n                                        </h4>\r\n                                        <v-text-field v-model=\"form.nombre\" required dense outlined\r\n                                            :rules=\"obligatorioRules\"></v-text-field>\r\n                                    </v-col>\r\n\r\n                                    <v-col cols=\"12\" sm=\"6\" md=\"5\" class=\"pb-0\">\r\n                                        <h4 class=\"pb-0 grey--text text--darken-2\">\r\n                                            Dependencia\r\n                                        </h4>\r\n\r\n                                        <v-autocomplete\r\n                                            v-model=\"form.dependencia\"\r\n                                            clearable\r\n                                            dense\r\n                                            outlined\r\n                                            :items=\"dependencias\"\r\n                                            :filter=\"customFilterDEP\"\r\n                                            item-value=\"dependencia\"\r\n                                            item-text=\"dependencia\"\r\n                                            :search-input.sync=\"oficinas_search\"\r\n                                            required\r\n                                        >\r\n                                            <template v-slot:no-data>\r\n                                                <v-list-item>\r\n                                                    <v-list-item-title>\r\n                                                        <template>\r\n                                                            No hay registros\r\n                                                        </template>\r\n                                                    </v-list-item-title>\r\n                                                </v-list-item>\r\n                                            </template>\r\n                                    \r\n                                            <template v-slot:item=\"data\">\r\n                                                <v-list-item-content>\r\n                                                    <v-list-item-title v-html=\"data.item.iduoper\">\r\n                                                    </v-list-item-title>\r\n                                                    <v-list-item-subtitle>\r\n                                                        {{ data.item.dependencia }}\r\n                                                    </v-list-item-subtitle>\r\n                                                </v-list-item-content>\r\n                                            </template>\r\n                                        </v-autocomplete>\r\n                                        <!-- <v-text-field v-model=\"form.dependencia\" required dense outlined\r\n                                            :rules=\"obligatorioRules\"></v-text-field> -->\r\n                                    </v-col>\r\n\r\n                                </v-row>\r\n\r\n                                <div class=\"d-flex mt-3\">\r\n                                    <v-spacer></v-spacer>\r\n                                    <v-btn :disabled=\"!form_valid\" color=\"primary\" @click=\"Guardar()\">\r\n                                        Registrar\r\n                                    </v-btn>\r\n                                </div>\r\n                            </v-form>\r\n\r\n                        </v-card-text>\r\n\r\n                    </v-card>   \r\n                </v-dialog>\r\n                \r\n                    <v-card class=\"mt-3\">\r\n                        <v-card-title>\r\n                            <h4>Lista de oficinas</h4>\r\n                            <v-divider></v-divider>\r\n                            <v-btn class=\"success\" @click=\"dialog = true;\">Nuevo</v-btn>\r\n                        </v-card-title>\r\n\r\n                        <v-card-text>\r\n                            <div>\r\n                                <v-divider></v-divider>\r\n                                <div class=\"mt-2 d-flex\" style=\"justify-content: flex-end\">\r\n                                    <v-text-field\r\n                                        v-model=\"dep\"\r\n                                        outlined\r\n                                        clearable\r\n                                        label=\"Cod. Dep\"\r\n                                        style=\"max-width: 80px;\"\r\n                                        dense\r\n                                        hide-details\r\n                                        >\r\n                                    </v-text-field>\r\n                                    <v-text-field\r\n                                        class=\"ml-2\"\r\n                                        v-model=\"buscaroficina\"\r\n                                        style=\"calc(100% - 90px)\"\r\n                                        append-icon=\"mdi-magnify\"\r\n                                        outlined\r\n                                        dense\r\n                                        hide-details\r\n                                        >\r\n                                    </v-text-field>\r\n                                </div>\r\n                            </div>\r\n\r\n                            <v-simple-table>\r\n                                <template v-slot:default>\r\n                                <thead>\r\n                                    <tr>\r\n                                    <th class=\"text-left\">\r\n                                        Cod U. Operativa\r\n                                    </th>\r\n                                    <th class=\"text-left\">\r\n                                        Nombre\r\n                                    </th>\r\n                                    <th class=\"text-left\">\r\n                                        Dependencia\r\n                                    </th>\r\n                                    </tr>\r\n                                </thead>\r\n                                <tbody>\r\n                                    <tr\r\n                                    v-for=\"item in oficinas\"\r\n                                    :key=\"item.iduoper\"\r\n                                    >\r\n                                    <td>{{ item.iduoper }}</td>\r\n                                    <td>{{ item.nombre }}</td>\r\n                                    <td>{{ item.dependencia }}</td>\r\n                                    </tr>\r\n                                </tbody>\r\n                                </template>\r\n                            </v-simple-table>\r\n                            <div v-if=\"oficinas.length\" v-observe-visibility=\"handleScrolledToBottom\"></div>\r\n                        </v-card-text>\r\n\r\n                    </v-card>\r\n                </v-container>\r\n            </div>\r\n\r\n            \r\n        </div>\r\n    </div>\r\n</template> \r\n<script>\r\nimport Layout from \"@/Layouts/FacilitadorLayout\";\r\nimport axios from \"axios\";\r\n\r\nexport default {\r\n    metaInfo: { title: \"Usuarios\" },\r\n    layout: Layout,\r\n    props: {\r\n        roles: Array,\r\n    },\r\n\r\n    data: () => ({\r\n\r\n        loading_table: false,\r\n        form_valid: null,\r\n\r\n        dependencias:[],\r\n        oficinas_search:\"\",\r\n\r\n        form: {\r\n            iduoper: null,\r\n            nombre: null,\r\n            dependencia: \"\",\r\n        },\r\n\r\n        obligatorioRules: [(v) => !!v || \"*Obligatorio\"],\r\n        absolute: false,\r\n        drawer: true,\r\n\r\n        dialog:false,   \r\n        dep:'01',\r\n        page:1,\r\n        oficinas:[],\r\n        buscaroficina:\"\",\r\n\r\n    }),\r\n    computed: {\r\n        size_display: {\r\n            get: function () {\r\n                if (this.$vuetify.breakpoint.width > 960) {\r\n                    this.absolute = false;\r\n                    return true;\r\n                }\r\n                this.absolute = true;\r\n                return false;\r\n            },\r\n            set: function (newValue) { },\r\n        },\r\n    },\r\n    mounted() {\r\n          this.getDependencias()\r\n          this.getOficinas()\r\n        },\r\n    methods: {\r\n        async getDependencias(){\r\n            let res = await axios.get(\"/facilitador/getDependencias\");\r\n            this.dependencias = res.data.datos;\r\n            return res.data.datos.data;\r\n        },\r\n        async Guardar() {\r\n            if (this.$refs.form_ofic.validate()) {\r\n                let res = await axios.post(\r\n                    \"/facilitador/oficina\",\r\n                    this.form\r\n                );\r\n                // console.log(res.data);\r\n                if (res.data.estado) {\r\n                    this.$refs.form_ofic.reset();\r\n                    this.limpiar()\r\n                }\r\n            }\r\n//            console.log(this.form);\r\n            this.dialog = false;\r\n            this.getOficinas()\r\n        },\r\n\r\n        customFilterDEP(item, queryText, itemText) {\r\n            const dependencia = item.dependencia.toLowerCase();\r\n            const iduoper = item.iduoper.toLowerCase();\r\n            const searchText = queryText.toLowerCase();\r\n            return (\r\n                dependencia.indexOf(searchText) > -1 ||\r\n                iduoper.indexOf(searchText) > -1\r\n             );\r\n        },\r\n\r\n        async getOficinas(term = \"\") {\r\n            let res = await axios.post(\r\n                \"/facilitador/oficina/get-ofi-all?page=\" + this.page,\r\n                { term: this.buscaroficina, dependencia: this.dep}\r\n            );\r\n            if (this.page > 1){ this.oficinas.push(...res.data.datos.data)}\r\n            else { this.oficinas = res.data.datos.data; }\r\n        },\r\n        async getOficinasS() {\r\n            let res = await axios.post(\r\n                \"/facilitador/oficina/get-ofi-all?page=\" + this.page,\r\n                { term: this.buscaroficina, dependencia: this.dep}\r\n            );\r\n            this.oficinas.push(...res.data.datos.data);\r\n        },\r\n        handleScrolledToBottom(isVisible){\r\n            if(!isVisible){ return}        \r\n            this.page++;\r\n            this.getOficinas()\r\n        },\r\n        limpiar(){\r\n            this.form = null;\r\n        },\r\n\r\n    },\r\n    watch:{\r\n\r\n        async dep(){\r\n            this.page = 1;\r\n            this.bienesAF = [];\r\n            this.getOficinas()\r\n        },\r\n        async buscaroficina(val) {\r\n            this.page = 1;\r\n            this.bienesAF = [];\r\n            await this.getOficinas(val);            \r\n        },\r\n\r\n    }\r\n\r\n};\r\n</script>\r\n\r\n<style>\r\n.wrapper-page {\r\n    width: 100%;\r\n    height: 100%;\r\n    background-color: #fafafa;\r\n}\r\n\r\n.page-heading {\r\n    display: flex;\r\n    justify-content: space-between;\r\n}\r\n\r\n.page-details {\r\n    width: 256px;\r\n    border-bottom: 1px solid rgba(0, 0, 0, 0.1);\r\n    border-right: 1px solid rgba(0, 0, 0, 0.1);\r\n}\r\n\r\n.page-search {\r\n    width: calc(100% - 256px);\r\n    border-bottom: 1px solid rgba(0, 0, 0, 0.1);\r\n}\r\n\r\n.content-wrapper {\r\n    position: relative;\r\n    display: flex;\r\n    width: 100%;\r\n    height: 100%;\r\n}\r\n\r\n.content {\r\n    width: 100%;\r\n    margin-left: 256px;\r\n    transition: all 0.3s ease;\r\n}\r\n\r\n.content.full {\r\n    width: 100%;\r\n    margin-left: 0;\r\n}\r\n\r\n@media (max-width: 960px) {\r\n    .content {\r\n        margin-left: 0;\r\n    }\r\n\r\n    .page-details {\r\n        width: 200px;\r\n    }\r\n\r\n    .page-search {\r\n        width: calc(100% - 200px);\r\n    }\r\n}\r\n\r\n@media (max-width: 740px) {\r\n    .page-details {\r\n        width: 180px;\r\n    }\r\n\r\n    .page-search {\r\n        width: calc(100% - 180px);\r\n    }\r\n}\r\n</style>\r\n"],"sourceRoot":""}]);
